const app = getApp().globalData;
const {
	imgRemote
} = app;
export default {
	props: ['ware', 'config'],
	data() {
		return {
			imgRemote: imgRemote,
			token: uni.getStorageSync('cdj_token'),
			count:0
		}
	},
	methods: {
		showCart() {
			this.$emit('showCart')
		},
		detail() {
			this.count++;
			if (this.count != 1) return;
			setTimeout(() => {
				this.count = 0
			}, 1000)
			if (this.config.is_detail == 1) {
				uni.navigateTo({
					url: `/pages/index/shopdetail?id=${this.ware.id}`
				})
			}
		}
	}
}
